// Section
.offer-timing {
  background-color: rgba($color: #111822, $alpha: 0.1);
  padding-top: 58px;
  padding-bottom: 94px;

  @include tablet-max {
    padding-top: 35px;
    padding-bottom: 45px;
  }
}

// Title
.offer-timing h2 {
  font-weight: normal;
  font-size: 34px;
  line-height: 44px;
  color: #232126;
  margin: 0;
  margin-bottom: 35px;

  @include tablet-max {
    font-size: 30px;
  }
}

// List
.offer-timing__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 109px;
  width: 100%;

  @include tablet-max {
    column-gap: 55px;
  }

  @include mobile-max {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }
}

.offer-timing__list > li {
  position: relative;
  display: flex;
  flex-direction: column;
}

.offer-timing__list > li:not(:last-child) {
