  // }
}

// Text
.offer-timing__date,
.offer-timing__text {
  font-size: 20px;
  line-height: 27px;
  color: #000000;
  margin: 0;

  @include tablet-max {
    font-size: 18px;
    line-height: 25px;
  }
}

.offer-timing__date {
  font-weight: bold;
  margin-bottom: 20px;
  align-self: center;
}

.offer-timing__text {
  text-align: center;
}

//Icons
.offer-timing__icon {
  background-color: $gold-color;
  border-radius: 50%;
  padding: 20px;
  margin-bottom: 18px;
  align-self: center;
}

.offer-timing__icon--knock {
  animation: knock 4s infinite ease-in-out;
}

@keyframes knock {
  0% {transform: rotate(0);}
  50% {transform: rotate(45deg);}
  100% {transform: rotate(0);}
  }
